import React, { useEffect, useState } from "react";
/* 두 번쨰 ajax */
import axios from "axios";
import styled from "styled-components";

/* swiper */
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/pagination";
import { Pagination } from "swiper";

const Div = styled.div`
  margin: 0 auto;
  width: 1200px;
  .title {
    padding: 50px 0;
    display: flex;
    justify-content: center;
    span {
      text-indent: -999999999px;
      background: url(../img/h_event.png) no-repeat;
      width: 87px;
    }
  }

  .swiper-slide {
    div {
      &:first-child {
        width: 100%;
        img {
          width: 100%;
        }
      }
      &:nth-child(2) {
        width: 100%;
        img {
          margin: 22px 0 8px;
        }
      }
    }
    .p {
      font-size: 16px;
    }
    .p2 {
      font-size: 13px;
      margin: 11px 0;
      color: #948780;
      padding-bottom: 40px;
    }
  }
  /* navigation 부분을 커스텀 해주었다 banner 부분과 달리 선택된 부분은 사이즈가 더 커야 한다 */
  .swiper-pagination-bullet {
    width: 8px;
    height: 8px;
    background-color: #cdccd5;
    opacity: 1;
    margin: 0 10px !important;
    &:hover {
      background: #483834;
      transition: all 0.3s;
    }
  }
  .swiper-pagination-bullet-active {
    width: 10px;
    height: 10px;
    background: #483834;
  }
`;
const Event = () => {
  /* axios 영역 */
  const [Data, setData] = useState([]);

  useEffect(() => {
    (async () => {
      let json = null;
      try {
        const response = await axios.get("http://localhost:3008/event");
        json = response.data;
      } catch (e) {
        console.error(e);
        alert("Ajax 연동 실패");
      }
      if (json != null) {
        // 성능 향상을 위한 함수형 업데이트 적용
        setData((Data) => json);
      }
    })();
  }, []);
  console.log(Data);

  return (
    <Div>
      <br />
      <div class="title">
        <span>BR EVENT</span>
      </div>
      <br />
      <Swiper
        initialSlide={1}
        className="swiper"
        spaceBetween={16}
        slidesPerView={4}
        slidesPerGroup={3}
        modules={[Pagination]}
        pagination={{ clickable: true }}
      >
        {Data.map((v, i) => {
          return (
            <SwiperSlide key={i} className="swiper-slide">
              <div>
                <img src={v.img} alt="img" />
              </div>
              <div>
                <img src={v.img2} alt="img" />
              </div>
              <p className="p">{v.ment}</p>
              <p className="p2">상시진행</p>
            </SwiperSlide>
          );
        })}
      </Swiper>
    </Div>
  );
};

export default Event;
