import React from "react";
import { Helmet, HelmetProvider } from "react-helmet-async";

const Meta = (props) => {
  return (
    <HelmetProvider>
      <Helmet>
        <meta charset="utf-8" />
        <title>{props.title}</title>
        {/* SEO 태그 */}
        <meta name="description" content={props.description} />
        <meta name="keywords" content={props.keywords} />
        <meta name="author" content={props.author} />
        <meta property="og:type" content="website" />
        <meta property="og:title" content={props.title} />
        <meta property="og:description" content={props.description} />
        <meta property="og:image" content={props.image} />
        <meta property="og:url" content={props.url} />

        <link rel="shortcut icon" href={props.image} type="image/png" />
        <link rel="icon" href={props.image} type="image/png" />
        <link rel="preconnect" href={props.font} type="text/css" />
        <link rel="preconnect" href={props.font2} type="text/css" crossorigin />
        <link href={props.connect} rel="stylesheet" type="text/css" />
        {/* 폰트 적용하기 */}
        <link
          href="https://hangeul.pstatic.net/hangeul_static/css/nanum-barun-gothic.css"
          rel="stylesheet"
        />
        {/* 추가적으로 적용해야 할 외부 js나 css로 여기서 명시할 수 있다. */}
        <style type="text/css">
          {`*{
                  font-family: 'NanumBarunGothic',"Nanum Barun Gothic", "나눔고딕",
                  "NanumGothic", "돋움", "Dotum", sans-serif;
                  list-style: none;
                  text-decoration: none;
                  box-sizing: border-box;
                  }
                  html{
                    min-width: 1200px;
                    width:100%;
                    margin: 0 auto;
                  }
                  body{
                      margin: 0;
                      padding: 0;
                      height: 100%;
                  }

                  img{
                    // width: 100%;
                  }
                  .img{
                    position: relative;
                  }
                  `}
        </style>
      </Helmet>
    </HelmetProvider>
  );
};
/*  props */
Meta.defaultProps = {
  title: "React 클론코딩",
  description: "baskinrobbins 클론토딩입니다.",
  keywords: "React",
  author: "sujin choe",
  url: window.location.href,
};

export default Meta;
