import React from "react";
import styled from "styled-components";

const Div = styled.div`
  width: 1200px;
  height: 547px;
  margin: auto;
  display: table;
  section {
    display: table-cell;

    h3 {
      text-align: center;
      padding: 95px 0 50px 0;
      margin: 0;
    }
  }
`;
const deliver = () => {
  return (
    <Div>
      <section>
        <h3>
          <img src="../img/h_store.png" alt="img" />
        </h3>
        <img src="../img/img_store.jpg" alt="img" />
      </section>
      <section>
        <h3>
          <img src="../img/h_happyorder_delivery.png" alt="img" />
        </h3>
        <img src="../img/img_happyorder_delivery.png" alt="img" />
      </section>
    </Div>
  );
};

export default deliver;
