import React from "react";
import styled from "styled-components";
import { NavLink } from "react-router-dom";

const Div = styled.div`
  background: url(../img/bg_menu.jpg) no-repeat center;
  height: 1200px;
  .img {
    height: 100%;
    background: url(../img/img_menu_list_220429.png) no-repeat center;
    h3 {
      padding-top: 78px;
      text-indent: -99999px;
      height: 22px;
      background: url(../img/h_menu.png) no-repeat;
      position: relative;
      left: 49%;
      top: 7%;
    }
    .icecream {
      display: block;
      position: relative;
      left: 40.3%;
      top: 7%;
      width: 395px;
      height: 301px;
    }
    .icecreamcake {
      display: block;
      position: relative;
      left: 56%;
      top: -20%;
      width: 276px;
      height: 472px;
    }
    .beverage {
      display: block;
      position: relative;
      left: 33%;
      top: -30%;
      width: 230px;
      height: 366px;
    }
    .coffee {
      display: block;
      position: relative;
      left: 43%;
      bottom: 62%;
      width: 330px;
      height: 290px;
    }
    .gift {
      display: block;
      position: relative;
      left: 55.5%;
      bottom: 73%;
      width: 276px;
      height: 183px;
    }
    .dessert {
      display: block;
      position: relative;
      left: 43%;
      bottom: 74%;
      width: 329px;
      height: 177px;
    }
  }
`;

const Menu = () => {
  return (
    <Div>
      <div className="img">
        <h3>BR MENU</h3>
        <br />
        <NavLink to="#" className="icecream"></NavLink>
        <NavLink to="#" className="icecreamcake"></NavLink>
        <NavLink to="#" className="beverage"></NavLink>
        <NavLink to="#" className="coffee"></NavLink>
        <NavLink to="#" className="gift"></NavLink>
        <NavLink to="#" className="dessert"></NavLink>
      </div>
    </Div>
  );
};

export default Menu;
