/**
 * @filename = insta.js
 * @description = section 영역 중 insta component
 * @author: sujin Choe (sujin971008@gamil.com)
 */
import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { NavLink } from "react-router-dom";
/* 네 번째 ajax */
import axios from "axios";

const Div = styled.div`
  width: 1200px;
  height: 1120px;
  margin: auto;
  h3 {
    text-align: center;
  }
  .sns {
    display: flex;
    justify-content: center;
    padding: 0px;
    li {
      padding: 0 9px;
    }
  }
  h4 {
    height: 1px;
    width: 100%;
    background-color: #dcd7d1;
    position: relative;
    text-align: center;
    margin: 50px 0;
    img {
      position: absolute;
      left: 38%;
    }
  }
  .insta {
    display: flex;
    flex-wrap: wrap;
    width: 1200px;
    padding: 20px 0;
    margin-top: 30px;

    li {
      width: 20%;
      height: 237px;
      box-sizing: border-box;
      border: 1px solid white;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
`;
const Insta = () => {
  /* axios */
  const [Data, setData] = useState([]);

  useEffect(() => {
    (async () => {
      let json = null;
      try {
        const response = await axios.get("http://localhost:3008/insta");
        json = response.data;
      } catch (e) {
        console.error(e);
        alert("Ajax 연동 실패");
      }
      if (json != null) {
        // 성능 향상을 위한 함수형 업데이트 적용
        setData((Data) => json);
      }
    })();
  }, []);
  console.log(Data);

  return (
    <Div>
      <h3>
        <img src="../img/h_sns.png" alt="img" />
      </h3>
      <ul className="sns">
        <li>
          <img src="../img/sns_facebook.png" alt="img" />
        </li>
        <li>
          <img src="../img/sns_twitter.png" alt="img" />
        </li>
        <li>
          <img src="../img/sns_blog.png" alt="img" />
        </li>
        <li>
          <img src="../img/sns_instagram.png" alt="img" />
        </li>
        <li>
          <img src="../img/sns_youtube.png" alt="img" />
        </li>
      </ul>
      <h4>
        <img src="../img/tit_sns.png" alt="title" />
      </h4>
      <ul className="insta">
        {Data.map((v, i) => {
          return (
            <li key={i}>
              <NavLink to="#">
                <img src={v.img} alt="insta" />
              </NavLink>
            </li>
          );
        })}
      </ul>
    </Div>
  );
};

export default Insta;
